.wrapper {
  background-color: #1F1F1F;
  min-height: 100vh;
}

body {
  background-color: #1F1F1F;
}

._1 {
  @include adaptive(padding-top, 16, 24);
  @include adaptive(padding-bottom, 0, 0);

  .logo_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include adaptive(margin-bottom, 16, 24);

    @media (max-width: 780px) {
      flex-direction: column;
      
      &__left {
        order: 2;
        width: 100%;
      }

      &__right {
        order: 1;

        @media (max-width: 560px) {
          margin-bottom: 12px;
        }
      }
    }

    &__left {
      display: flex;
      align-items: center;

      .logo {
        @include adaptive(margin-right, 16, 40);
        @include adaptive(max-width, 84, 110);

      }

      h2 {
        font-family: Inter;
        font-weight: 600;
        @include adaptive(font-size, 10, 16);
        line-height: 170%;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: #FFFFFF;
      }
    }

    &__right {
      margin-left: auto;
      display: flex;
      align-items: center;

      .mail, .phone {
        display: flex;
        align-items: center;

        img {
          width: 100%;
          @include adaptive(max-width, 16, 24);
          @include adaptive(margin-right, 4, 8);
        }

        a {
          font-family: Inter;
          font-weight: 500;
          @include adaptive(font-size, 12, 20);
          line-height: 170%;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: #FFFFFF;
        }
      }

      .phone {
        @include adaptive(margin-right, 16, 80);
      }
    }
  }

  .img_box {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 540px) {
      flex-direction: column;

      &__left {
        order: 2;
      }

      &__right {
        order: 1;
        max-width: 320px;
        margin-bottom: 24px;

        width: 100%;

        img {
          align-items: start;
          margin-right: auto;
        }
        margin-right: auto;
      }
    }

    &__left {
      max-width: 680px;

      h2 {
        font-family: Inter;
        font-weight: 700;
        @include adaptive(font-size, 48, 56);
        line-height: 130%;
        letter-spacing: 4%;
        text-transform: uppercase;
        color: #FFFFFF;
        @include adaptive(margin-bottom, 16, 40);

        @media (max-width: 1280px) {
          @include adaptive(font-size, 24, 56);
        }
      }

      p {
        font-family: Inter;
        font-weight: 400;
        @include adaptive(font-size, 16, 24);
        line-height: 130%;
        letter-spacing: 4%;
        color: #FFFFFF;
        @include adaptive(margin-bottom, 20, 60);
      }
      
      .button {
        display: inline-block;
        @include adaptive(padding, 20, 86);
        @include adaptive(padding-top, 16, 32);
        @include adaptive(padding-bottom, 16, 32);
        border-radius: 10000px;
        font-family: Inter;
        font-weight: 600;
        @include adaptive(font-size, 14, 20);
        line-height: 100%;
        letter-spacing: 4%;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        background-color: #9452D1;
      }
    }

    &__right {
      img {
        width: 100%;
        max-width: 600px; 

        @media (max-width: 1300px) {
          @include adaptive(max-width, 480, 600);
        }
      }
    }
  }

  @media (max-width: 767px) {
    padding-bottom: 0;
  }
}


._2 {
  @include adaptive(padding-top, 26, 120);
  padding-bottom: 0;

  h2 {
    font-family: Inter;
    font-weight: 700;
    @include adaptive(font-size, 20, 40);
    line-height: 130%;
    letter-spacing: 4%;
    text-transform: uppercase;
    color: #FFFFFF;
    @include adaptive(margin-bottom, 20, 60);
  }

  .box_content {
    display: flex;
    align-items: center;

    &__left {
      @include adaptive(margin-right, 24, 70);
      @include adaptive(max-width, 320, 521);
      @include adaptive(min-width, 320, 521);
    }

    &__right {
      @media (max-width: 540px) {
        margin-right: auto;
      }
      .icon_box1 {
        @include adaptive(margin-bottom, 20, 60);
      }

      .icon_box {
        display: flex;

        @media (max-width: 540px) {
          flex-direction: column;
          justify-content: start;
          align-items: start;

          &__item {
            width: 100%;
            margin: 0;
            margin-bottom: 24px;
            margin-right: auto !important;

            p{
              width: 100%;
              max-width: 300px;
              margin-right: auto;
            }

            &:last-child {
              margin-bottom: 0;
            }
          }
        }

        &__item {
          width: 100%;
          &:first-child {
            @include adaptive(margin-right, 24, 50);
          }

          img {
            @include adaptive(margin-bottom, 12, 20);
            @include adaptive(max-width, 48, 80);
            width: 100%;
          }

          h3 {
            font-family: Inter;
            font-weight: 700;
            @include adaptive(font-size, 16, 24);
            line-height: 150%;
            letter-spacing: 4%;
            text-transform: uppercase;
            color: #FFFFFF;
            @include adaptive(margin-bottom, 8, 16);
          }

          p {
            font-family: Inter;
            font-weight: 400;
            @include adaptive(font-size, 14, 18);
            line-height: 130%;
            letter-spacing: 0%;
            color: #FFFFFF;
          }
        }
      }
    }

    @media (max-width: 760px) {
      flex-direction: column;

      &__left {
        margin-top: 20px;
        width: 100% !important;
        text-align: left;
        margin-right: auto;
        order: 2;
      }

      &__right {
        order: 1;
      }
    }
  }
}

._3 {
  @include adaptive(padding-top, 26, 120);
  padding-bottom: 0;

  h2 {
    font-family: Inter;
    font-weight: 700;
    @include adaptive(font-size, 20, 40);
    line-height: 130%;
    letter-spacing: 4%;
    text-transform: uppercase;
    color: #FFFFFF;
    @include adaptive(margin-bottom, 20, 60);
  }

  .tab_bar {
    display: flex;
    flex-wrap: wrap;
    @include adaptive(gap, 8, 12);
    @include adaptive(margin-bottom, 20, 60);

    
    .tab {
      border: 1px solid #FFFFFF99;
      border-radius: 10000px;
      padding: 8px;
      padding-left: 16px;
      padding-right: 16px;
      cursor: pointer;


      p {
        font-family: Inter;
        font-weight: 400;
        @include adaptive(font-size, 16, 24);
        line-height: 100%;
        letter-spacing: 0px;
        color: #FFFFFF99;
      }

      &.active {
        background-color: #9452D1;
        border-color: #9452D1;

        p {
          color: #fff;
          transition: all 0.6s ease;
        }
      }
    }
  }

  .tab_content__1, .tab_content__2, .tab_content__3, .tab_content__4, .tab_content__5, .tab_content__6 {
    &.active {
      .items {
        display: flex;
      }
    }
  }

  .items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5%;
    display: none;

    @media (max-width: 760px) {
      flex-direction: column;
    }

    &__item {
      width: 30%;

      img {
        width: 100%;
        height: 100%;
        @include adaptive(max-width, 160, 300);
        @include adaptive(max-height, 120, 240);
        @include adaptive(margin-bottom, 16, 32);
      }

      h3 {
        font-family: Inter;
        font-weight: 700;
        @include adaptive(font-size, 16, 24);
        line-height: 150%;
        letter-spacing: 4%;
        text-transform: uppercase;
        color: #fff;
        @include adaptive(margin-bottom, 12, 16);
      }

      p {
        font-family: Inter;
        font-weight: 400;
        @include adaptive(font-size, 14, 18);
        line-height: 130%;
        letter-spacing: 0%;
        color: #fff;
      }
    }

    @media (max-width: 760px) {
      flex-direction: column;
      
      &__item {
        width: 100%;
        display: flex;
        max-width: 480px;
        align-items: center;
        margin-bottom: 24px;

        &:last-child {
          margin-bottom: 0;
        }

        div {
          margin-left: 16px;
        }

        @media (max-width: 500px) {
          flex-direction: column;

          img {
            margin-right: auto;
          }

          div {
            margin-left: 0;
          }
        }
      }
    }
  }
}

